/*--------------------------------------------------------------
# Layouts - Header 
--------------------------------------------------------------*/
header.site-header {
  background-color: white;
  color: white;
  z-index: 99;
}

header.site-header nav.navigation__desktop {
  max-width: 1370px;
  width: 100%;
  margin: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
}

@media (max-width: 900px) {
  header.site-header nav.navigation__desktop {
    display: none;
  }
}

header.site-header nav.navigation__desktop div.navigation__desktop-logo a {
  text-decoration: none;
  color: black;
}

header.site-header nav.navigation__desktop div.navigation__desktop-logo a h1.no--logo {
  font-family: 'Libre Baskerville', serif;
  font-size: 28px;
}

header.site-header nav.navigation__desktop div.navigation__desktop-menu {
  width: 100%;
}

header.site-header nav.navigation__desktop div.navigation__desktop-menu div.menu-primary-container ul {
  color: black;
  margin: 0;
  display: flex;
  justify-content: flex-end;
  font-family: 'Open Sans', sans-serif;
  list-style: none;
  padding-left: 0;
}

header.site-header nav.navigation__desktop div.navigation__desktop-menu div.menu-primary-container ul li a {
  text-decoration: none;
  text-transform: uppercase;
  padding: 10px 15px;
  font-family: 'Open Sans', sans-serif;
}

header.site-header nav.navigation__desktop div.navigation__desktop-menu div.menu-primary-container ul li a:hover {
  color: grey;
}

header.site-header nav.navigation__mobile {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 60px;
  padding: 0 15px;
  position: relative;
}

@media (min-width: 900px) {
  header.site-header nav.navigation__mobile {
    display: none;
  }
}

header.site-header nav.navigation__mobile div.navigation__mobile-logo a {
  text-decoration: none;
  color: black;
}

header.site-header nav.navigation__mobile div.navigation__mobile-logo a h1.no--logo-mobile {
  font-family: 'Libre Baskerville', serif;
  font-size: 22px;
}

header.site-header nav.navigation__mobile div.navigation__nav-toggle {
  width: 36px;
  height: 28px;
  position: relative;
  transform: rotate(0deg);
  transition: .5s ease-in-out;
  cursor: pointer;
}

header.site-header nav.navigation__mobile div.navigation__nav-toggle span {
  display: block;
  position: absolute;
  height: 3px;
  width: 100%;
  background: black;
  border-radius: 9px;
  opacity: 1;
  left: 0;
  transform: rotate(0deg);
  transition: .25s ease-in-out;
}

header.site-header nav.navigation__mobile div.navigation__nav-toggle span:nth-child(1) {
  top: 0px;
  transform-origin: left center;
}

header.site-header nav.navigation__mobile div.navigation__nav-toggle span:nth-child(2) {
  top: 12px;
  transform-origin: left center;
}

header.site-header nav.navigation__mobile div.navigation__nav-toggle span:nth-child(3) {
  top: 25px;
  transform-origin: left center;
}

header.site-header nav.navigation__mobile div.navigation__nav-toggle.open span:nth-child(1) {
  transform: rotate(45deg);
  top: 0px;
  left: 8px;
}

header.site-header nav.navigation__mobile div.navigation__nav-toggle.open span:nth-child(2) {
  width: 0%;
  opacity: 0;
}

header.site-header nav.navigation__mobile div.navigation__nav-toggle.open span:nth-child(3) {
  transform: rotate(-45deg);
  top: 26px;
  left: 8px;
}

header.site-header nav.navigation__mobile div.navigation__mobile-items {
  position: absolute;
  top: 60px;
  left: 0;
  width: 100%;
  transform: translateX(-1000%);
  transition: transform 0.6s ease;
  z-index: 20;
  height: calc(100vh - 60px);
  background-color: white;
}

header.site-header nav.navigation__mobile div.navigation__mobile-items.open {
  transform: translateX(0);
}

header.site-header nav.navigation__mobile div.navigation__mobile-items div.menu-primary-container ul.menu {
  margin: 0;
  padding: 0;
}

header.site-header nav.navigation__mobile div.navigation__mobile-items div.menu-primary-container ul.menu li {
  list-style: none;
  background-color: white;
}

header.site-header nav.navigation__mobile div.navigation__mobile-items div.menu-primary-container ul.menu li:first-child {
  margin-top: 20px;
}

header.site-header nav.navigation__mobile div.navigation__mobile-items div.menu-primary-container ul.menu li a {
  font-family: 'Open Sans', sans-serif;
  text-decoration: none;
  color: black;
  display: block;
  padding: 15px 20px;
  text-transform: uppercase;
  font-weight: 500;
  font-weight: bold;
  text-align: right;
}

/*--------------------------------------------------------------
# Layouts - Footer
--------------------------------------------------------------*/
footer.site-footer {
  color: white;
}

footer.site-footer div.footer-container {
  color: black;
  min-height: 200px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

footer.site-footer div.footer-container h3 {
  font-family: 'Libre Baskerville', serif;
  font-size: 22px;
}

/*--------------------------------------------------------------
Pages
--------------------------------------------------------------*/
div.error-404-wrapper {
  height: 80vh;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

div.error-404-wrapper h1 {
  max-width: 580px;
}
